<template>
    <div class="row">
        <!-- "@click.native" allows to click the "app-quote" but in reality
            The click event is actually the event of the "native" html part inside of the "app-quote" -->
        <app-quote
            v-for="(quote, index) in quotes"
            @click.native="deleteQuote(index)"
            :key="quote + index"
        >
            {{ quote }}
             <!-- - {{ index }} -->
        </app-quote>
    </div>
</template>

<script>
import Quote from './Quote'

export default {
    props: ['quotes'],
    components: {
        appQuote: Quote
    },
    methods: {
        deleteQuote(index) {
            this.$emit('quoteDeleted', index)
        }
    }
}
</script>

